// TechDetail Component

import React from 'react'
import { useHistory } from "react-router-dom";

import './tech-detail.scss'
import getTech from '../../../services/techs'
import {getCert} from '../../../services/certs'
import CertBubble from '../../../components/CertBubble'
import TechLevel from '../../../components/TechLevel'
import TechBubble from '../../../components/TechBubble'

const TechDetail = ({ match }) => {
  let history = useHistory();
  let urlTech = match.params.tech
  const tech = getTech(urlTech)
  let techImage = ""
  if(tech) {
    techImage = require(`../../../assets/images/techs/${tech.img}`)
  } else {
    history.push('/technologies')
  }

  return techImage === "" ? null : (
    <div className="techDetail">
      <h2>{tech.name}</h2>
      <div className="techDetail-header">
        <figure className="techDetail-img">
          <img src={techImage.default} alt={tech.name} loading="lazy" />
          <TechLevel level={tech.level} />
        </figure>
        <p>{tech.desc}</p>
      </div>
      {
        !tech.certs.length ? false :
        <section className="techDetail-certs">
          <h3>Cursos y Certificaciones</h3>
          <div className="techDetail-certsList">
            {
              tech.certs.map(id => {
                const cert = getCert('id', id)
                return <CertBubble {...cert} key= {id} />
              })
            }
          </div>
        </section>
      }
      {/* <section className="techDetail-projects">
        <h3>Utilización en Proyectos</h3>

      </section> */}
      <section className="techDetail-related">
        <h3>Tecnologías Relacionadas</h3>
        <div className="techDetail-relatedList">
          {
            tech.rel.map((item) => {
              const relTech = getTech(item);
              return <TechBubble {...relTech} key={relTech.id} />
            })
          }
        </div>
      </section>
    </div>
  )
}

export default TechDetail
