// TechBubble Component

import React from 'react'
import './tech-bubble.scss'
import {Link} from 'react-router-dom'


const TechBubble = ({ img, name, url, }) => {
  let image = require(`../../assets/images/techs/${img}`);
  
  return (
    <figure className="techBubble">
      <Link to={`/technologies/${url}`} aria-label={name}>
        <img src={image.default} alt={name} loading="lazy" />
      </Link>
      <figcaption>
        {name}
      </figcaption>
    </figure>
  )
}

export default TechBubble
