import React from 'react'
import './cert-bubble.scss'
import {Link} from 'react-router-dom'

const CertBubble = ({img, name, uri}) => {

  let image = require(`../../assets/images/techs/${img}`);
  
  return (
      <figure className="certBubble">
      <Link to={`/training/${uri}`} aria-label={name}>
        <img src={image.default} alt={name} loading="lazy" />
      </Link>
      <figcaption>
        {name}
      </figcaption>
    </figure>
  )
}

export default CertBubble
