// TechLevel Componet

import React, { lazy } from 'react'
import './tech-level.scss'

const TechLevel = ({level}) => {
  let stars = [];
  for(let i=0; i < 5; i++) {
    if( i < level) {
      const StartFull = lazy(() => import('../icons/StarFullIcon')) 
      stars.push(<StartFull key={i} />)
    } else {
      const StartEmpty = lazy(() => import('../icons/StarEmptyIcon'))
      stars.push(<StartEmpty key={i} />)
    }
  }
  return(
    <figcaption className="tech-level">
      { stars }
    </figcaption>
  )
}

export default TechLevel
